import { useInterval } from "./useInterval";
import { useEffect, useRef, useState } from "react";
import { getFavicon } from "../utils/getFavicon";
import { UsePleaseStayOptions } from "../types/UsePleaseStayOptions";

export const useFaviconChangeEffect = (
  faviconLinks: UsePleaseStayOptions["faviconLinks"],
  shouldIterateFavicons: boolean,
  interval: number,
  shouldAlwaysPlay: boolean
) => {
  const [faviconIndex, setFaviconIndex] = useState(0);
  const faviconRef = useRef<HTMLLinkElement>(getFavicon());
  const originalFaviconHref = useRef(faviconRef.current?.href);

  useInterval(
    () => {
      if (faviconLinks !== undefined) {
        const nextIndex = faviconIndex + 1;
        nextIndex === faviconLinks.length
          ? setFaviconIndex(0)
          : setFaviconIndex(nextIndex);
      }
    },
    interval,
    shouldIterateFavicons
  );

  useEffect(() => {
    if (faviconLinks !== undefined) {
      faviconRef.current.href = faviconLinks[faviconIndex];
    }
    if (
      !shouldIterateFavicons &&
      !shouldAlwaysPlay &&
      faviconRef.current &&
      originalFaviconHref.current
    ) {
      faviconRef.current.href = originalFaviconHref.current;
    }
  }, [faviconIndex, faviconLinks, shouldIterateFavicons, shouldAlwaysPlay]);

  useEffect(() => {
    return () => {
      if (faviconRef.current && originalFaviconHref.current) {
        faviconRef.current.href = originalFaviconHref.current;
      }
    };
  }, []);
};
