import { useEffect } from "react";

export const useListenToVisibilityChangeOnMount = (
  setShouldIterateTitles: (shouldIterateTitles: boolean) => void,
  shouldAlwaysPlay: boolean
) => {
  const handleVisibilityChange = () => {
    setShouldIterateTitles(document.visibilityState !== "visible");
  };

  useEffect(() => {
    if (shouldAlwaysPlay) {
      setShouldIterateTitles(true);
      return;
    }
    document.addEventListener("visibilitychange", handleVisibilityChange);
    return () => {
      document.removeEventListener("visibilitychange", handleVisibilityChange);
    };
  }, [shouldAlwaysPlay]);
};
